.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
}
.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  margin: 0;
}
.button {
  width: 100%;
  border: 2px solid #444444;
  color: #444444;
  margin: 0;
  padding: 16px 16px;
  text-align: center;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #fff;
  cursor: pointer;
}
.button:hover {
  background-color: #444444;
  color: #fff;
}
.button:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}
.copyright__text {
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  max-width: 130px;
  margin: 0;
}
.copyright__icon {
  display: inline-block;
  margin-left: 26px;
}
.copyright__link {
  display: flex;
  color: #000;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  fill: #63d1bb;
  max-width: 230px;
}
.copyright__link:hover {
  fill: #46c1ae;
}
.copyright__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .copyright__text {
    margin-top: -2px;
  }
}
.choice {
  display: flex;
  align-items: center;
  margin-bottom: 13px;
}
.choice input:checked + .choice__mark::after {
  opacity: 1;
}
.choice:hover .choice__mark {
  border-color: #62d1ba;
}
.choice:active .choice__mark {
  border-color: rgba(0, 0, 0, 0.3);
}
.choice--checkbox {
  margin-bottom: 21px;
}
.choice__mark {
  position: relative;
  display: block;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: 2px solid #444444;
  margin-right: 18px;
}
.choice__mark::after {
  position: absolute;
  right: 50%;
  bottom: 50%;
  background-color: #62d1ba;
  transform: translate(50%, 50%);
  border-radius: 50%;
  width: 16px;
  height: 16px;
  content: "";
  opacity: 0;
}
.choice__mark--checkbox {
  border-radius: 0%;
  margin-right: 23px;
}
.choice__mark--checkbox::after {
  background-image: url("../img/checkmark.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  width: 100%;
  height: 100%;
  background-size: 70%;
  border-radius: 0%;
}
@media (min-width: 768px) {
  .choice {
    margin-bottom: 25px;
  }
  .choice__hyphenation {
    display: none;
  }
  .choice__mark {
    width: 47px;
    height: 47px;
  }
  .choice__mark--checkbox {
    margin-right: 16px;
  }
  .choice__label {
    font-weight: 700;
    font-size: 19px;
    line-height: 18px;
    margin-left: 10px;
  }
}
@media (min-width: 768px) {
  .choice__label {
    margin-left: 12px;
  }
}
