  border: 1px solid #ececec;
  box-sizing: border-box;
}
.tutorial__wrapper:hover {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
}
.tutorial__wrapper:active {
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}
.tutorial__title {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  padding: 0 44px 0 91px;
  margin: 21px 0 26px 0;
  background-image: url("../img/video-icon.svg");
  background-repeat: no-repeat;
  background-position: top 10px left 35px;
}
.tutorial__video-wrapper {
  position: relative;
}
.tutorial__video {
  display: block;
}
.tutorial__play-icon {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  fill: #63d1bb;
}
.tutorial__play-icon:hover {
  width: calc(101px*1.2);
  height: calc(77px*1.2);
}
.tutorial__play-icon:active {
  width: calc(101px*0.8);
  height: calc(77px*0.8);
  fill: #d0f1eb;
}
.tutorial__description {
  font-size: 13px;
  line-height: 24px;
  color: #444444;
  margin: 38px 0 40px 34px;
}
